import { openDB, IDBPDatabase } from 'idb';

const DB_PREFIX = 'infinitalk';

export enum StoreName {
    ThemesNames = 'themesNames',
    ThemesContents = 'themesContents'
}

function buildDBName(languageCombination: string /* ex: "frenchToEnglish" */): string {
    return DB_PREFIX + '_' + languageCombination;
}

async function openDBandStore(languageCombination: string /* ex: "frenchToEnglish" */): Promise<IDBPDatabase> {
    return openDB(buildDBName(languageCombination), undefined, {
        upgrade(db) {
            if (!db.objectStoreNames.contains(StoreName.ThemesNames)) {
                console.info(`IndexedDB create store. store: ${StoreName.ThemesNames}`);
                db.createObjectStore(StoreName.ThemesNames);
            }
            if (!db.objectStoreNames.contains(StoreName.ThemesContents)) {
                console.info(`IndexedDB create store. store: ${StoreName.ThemesContents}`);
                db.createObjectStore(StoreName.ThemesContents);
            }
        }
    });
}

export async function write(languageCombination: string /* ex: "frenchToEnglish" */, storeName: StoreName, key: string, value: string): Promise<void> {
    const db: IDBPDatabase = await openDBandStore(languageCombination);
    try {
        console.info(`IndexedDB write. store: ${storeName} | key: ${key} | value: ${value}`);
        db.put(storeName, value, key);
    } catch(ex) {
        console.error(`Failed IndexedDB write. store: ${storeName} | key: ${key} | value: ${value}. Exception: ${ex}`);
    } finally {
        db.close();
    }
}

export async function read(languageCombination: string /* ex: "frenchToEnglish" */, storeName: StoreName, key: string): Promise<string | null> {
    const db: IDBPDatabase = await openDBandStore(languageCombination);
    let value: string | null = null;
    try {
        console.info(`IndexedDB read. store: ${storeName} | key: ${key}`);
        value = await db.get(storeName, key);
        console.info(`IndexedDB read. Got value ${value}`);
    } catch(ex) {
        console.error(`Failed IndexedDB read. store: ${storeName} | key: ${key}. Exception: ${ex}`);
    } finally {
        db.close();
    }
    return value;
}

export async function getThemes(languageCombination: string /* ex: "frenchToEnglish" */): Promise<Map<string, string> /* Map: themeId -> themeName */> {
    const db: IDBPDatabase = await openDBandStore(languageCombination);
    let keys: string[] = [];
    let values: string[] = [];
    try {
        keys = (await db.getAllKeys(StoreName.ThemesNames)).map(key => key.toString());
        values = await db.getAll(StoreName.ThemesNames);
    } catch(ex) {
        console.error(`Failed IndexedDB getThemes. Exception: ${ex}`);
    }   finally {
        db.close();
    }
    let themesEntries: [string, string][] = keys.map((key, index) => [key, values[index]]);
    let themes: Map<string, string> = new Map(themesEntries);
    return themes;
}

