import { Row, Col, Card } from "antd";
import { useEffect, useState } from "react";
import { getThemes } from "../OfflineStorage";
import { Link } from "react-router-dom";
import Loading from "./Loading";

interface ThemeCollectionPageProps { }

const ThemeCollectionPage: React.FC<ThemeCollectionPageProps> = (props: ThemeCollectionPageProps) => {
    const [themes, setThemes] = useState<Map<string, string>>();

    useEffect(() => {
        const readThemesAsync = async () => {
            setThemes(await getThemes('englishToFrench'));
        }
        readThemesAsync();
    }, []);

    if (themes === undefined) {
        return (
            <Loading loadedTarget="themes" />
        );
    } else {
        return (
            <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
                <div style={{ height: '20vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Choose a theme</div>
                <Row gutter={16} style={{ width: "80vw", height: "50vh" }}>
                    {[...themes.keys()].map((themeId: string, index: number) => <Col span={4} key={index}>
                        <Card bordered={false} style={{ height: "100%", display: "flex", alignItems: "center", justifyContent: "center" }}>
                            <Link to={"/exercise/" + themeId}>
                                {themes.get(themeId)}
                            </Link>
                        </Card>
                    </Col>)}
                </Row>
            </div>
        );
    }
};

export default ThemeCollectionPage;
