import { Spin } from "antd";

interface LoadingProps {
    loadedTarget: string
}

const Loading: React.FC<LoadingProps> = (props: LoadingProps) => {
    return (
        <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
            <Spin tip={"Loading " + props.loadedTarget + " ..."}>
                <div style={{ width: "100vw", height: "100vh" }} />
            </Spin>
        </div>
    );
}

export default Loading;