import { Form, Select, Button } from "antd";
import { Link } from "react-router-dom";

const HomePage: React.FC = () => {
  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    console.log(values);
  };

  return (
      <div style={{ position: 'relative', left: '15vw', width: '40vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
        <div style={{ display: 'flex', position: 'relative', left: '12vw', height: '20vh', alignItems: 'center' }}>Welcome to Infinitalk</div>
        <Form
          form={form}
          name="control-hooks"
          onFinish={onFinish}
          style={{ maxWidth: 600 }}
        >
          <Form.Item name="personalLanguage" label="I speak:" rules={[{ required: true }]}>
            <Select
              placeholder="Select a option and change input text above"
              options={[
                { value: 'english', label: 'english' },
                { value: 'french', label: 'french' }
              ]}
            />
          </Form.Item>
          <Form.Item name="learntLanguage" label="I want to learn:" rules={[{ required: true }]}>
            <Select
              placeholder="Select a option and change input text above"
              options={[
                { value: 'english', label: 'english' },
                { value: 'french', label: 'french' }
              ]}
            />
          </Form.Item>
          <Form.Item name="level" label="Level" rules={[{ required: true }]}>
            <Select
              placeholder="Select a option and change input text above"
              options={[
                { value: 'beginner', label: 'beginner' },
                { value: 'intermediate', label: 'intermediate' },
                { value: 'advanced', label: 'advanced' }
              ]}
            />
          </Form.Item>
          <Form.Item>
            <Link to='/activity-choice'>
              <Button type="primary" htmlType="submit">Submit</Button>
            </Link>
          </Form.Item>
        </Form>
      </div>
  );
};

export default HomePage;
