import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import HomePage from './components/HomePage';
import ThemeCollectionPage from './components/ThemeCollectionPage';
import LessonPage from './components/LessonPage';
import ExercisePage from './components/ExercisePage';
import ScorePage from './components/ScorePage';
import ActivityChoicePage from './components/ActivityChoicePage';
import ThemeChoicePage from './components/ThemeChoicePage';

const router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
    children: [
      {
        index: true,
        element: <HomePage/>
      },
      {
        path: "activity-choice",
        element: <ActivityChoicePage /> 
      },
      {
        path: "new-theme",
        element: <ThemeChoicePage />
      },
      {
        path: "revisions",
        element: <ThemeCollectionPage />
      },
      {
        path: "lesson/presentations",
        element: <LessonPage themeId='presentations'/>
      },
      {
        path: "lesson/family",
        element: <LessonPage themeId='family'/>
      },
      {
        path: "exercise/presentations",
        element: <ExercisePage themeId='presentations' />
      },
      {
        path: "exercise/family",
        element: <ExercisePage themeId='family'/>
      },
      {
        path: "score",
        element: <ScorePage />
      }
    ]
  } // TODO: add error page
])

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
