import { Card } from "antd";

const ScorePage: React.FC<{}> = () => {
    return (
        <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
            <div style={{ height: '20vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Score</div>
            <Card title="Good!" style={{ width: "70vw", height: "50vh" }}>
                <h2>10/10</h2>
            </Card>
        </div>
    );
};

export default ScorePage;
