import { Row, Col, Card } from "antd";
import React from "react";
import { Link } from "react-router-dom";

interface ChoiceProps {
    title: string,
    firstLink: string,
    secondLink: string,
    firstLabel: string,
    secondLabel: string
}

const Choice: React.FC<ChoiceProps> = (props: ChoiceProps) => {
    return (
        <div>
            <div style={{ height: '20vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>{props.title}</div>
            <Row gutter={16} style={{ width: "70vw", height: "50vh" }}>
                <Col span={12}>
                    <Link to={props.firstLink}>
                        <Card bordered={false} style={{ height: "100%", display: "flex", alignItems: "center", justifyContent: "center", backgroundColor: "#F0F8FF" }}>
                            {props.firstLabel}
                        </Card>
                    </Link>
                </Col>
                <Col span={12}>
                    <Link to={props.secondLink}>
                        <Card bordered={false} style={{ height: "100%", display: "flex", alignItems: "center", justifyContent: "center", backgroundColor: "#F0F8FF" }}>
                            {props.secondLabel}
                        </Card>
                    </Link>
                </Col>
            </Row>
        </div>
    );
};

export default Choice;
