import { Button, Card } from "antd";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { StoreName, write } from "../OfflineStorage";
import { FRENCH_LESSONS_EXAMPLES } from "../stubs/lessonsExamples";
import { useThemeOptions } from "../App";
import Loading from "./Loading";

enum LessonStepType {
    NewStep = 'Next word',
    LessonEnd = 'Finish lesson'
};

interface LessonPageProps {
    themeId: string;
};

const LessonPage: React.FC<LessonPageProps> = (props) => {
    const [finishedLoading, setFinishedLoading] = useState<boolean>(false);
    const [lessonContent, setLessonContent] = useState<[string, string][] | null>(null);
    const [wordIndex, setWordIndex] = useState<number>(0);
    const [currentWord, setCurrentWord] = useState<string | null>(null);
    const [currentTranslation, setCurrentTranslation] = useState<string | null>(null);
    const [nextStepType, setNextStepType] = useState<LessonStepType>(LessonStepType.NewStep);

    const { newThemesOptions } = useThemeOptions();

    useEffect(() => {
        // TODO: load lesson from GPT
        write('englishToFrench', StoreName.ThemesContents, props.themeId, JSON.stringify(FRENCH_LESSONS_EXAMPLES[props.themeId]));
        write('englishToFrench', StoreName.ThemesNames, props.themeId, newThemesOptions?.get(props.themeId) ?? 'Unknown theme');
        const newLessonContent: [string, string][] = Object.entries(FRENCH_LESSONS_EXAMPLES[props.themeId]);
        // TODO: add theme name to lesson content
        setLessonContent(newLessonContent); 
        // TODO: remove this timeout
        setTimeout(() => setFinishedLoading(true), 3000);
        // setFinishedLoading(true);
    }, [newThemesOptions, props.themeId]);

    useEffect(() => {
        if (lessonContent !== null) {
            setCurrentWord(lessonContent[wordIndex][0]);
            setCurrentTranslation(lessonContent[wordIndex][1]);
        }
    }, [lessonContent, wordIndex]);

    if (!finishedLoading) {
        return (
            <Loading loadedTarget="lesson" />
        );
    } else {
        return (
            <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
                <div style={{ height: '20vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Let's learn!</div>
                <Card
                    title={currentWord}
                    style={{ width: "70vw", height: "50vh" }
                }>
                    <p>{currentTranslation}</p>
                    <Button
                        type="primary"
                        onClick={() => {
                            if (nextStepType === LessonStepType.NewStep) {
                                if (wordIndex === lessonContent!.length - 1) {
                                    setNextStepType(LessonStepType.LessonEnd);
                                } else {
                                    setWordIndex((previousWordIndex) => previousWordIndex + 1);
                                }
                            }
                        }}
                    >
                        {nextStepType === LessonStepType.NewStep ?
                            LessonStepType.NewStep :
                            <Link to='/activity-choice'>{LessonStepType.LessonEnd}</Link>
                        }
                    </Button>
                </Card>
            </div>
        );
    }
};

export default LessonPage;
