import { useEffect, useState } from 'react';
import './App.css';
import { read, write } from './OfflineStorage';
import LessonPage from './components/LessonPage';
import ExercisePage from './components/ExercisePage';
import ScorePage from './components/ScorePage';
import Choice from './components/Choice';
import ThemeCollectionPage from './components/ThemeCollectionPage';
import HomePage from './components/HomePage';
import { Menu } from 'antd';
import { ItemType, MenuItemType } from 'antd/es/menu/hooks/useItems';
import { Link, Outlet, useOutletContext } from 'react-router-dom';

let stuffStored = false;

function getItem(key: string, label: string, icon: React.ReactNode): MenuItemType {
  return {
    label,
    key,
    icon
  };
}

const items: ItemType<MenuItemType>[] = [
  getItem('home', 'Home', <Link to='/' />),
  getItem('new-theme', 'New theme', <Link to='/new-theme' />),
  getItem('revisions', 'Revisions', <Link to='/revisions' />),
];

function App() {

  let [readyToRead, setReadyToRead] = useState<boolean>(false);
  let [newThemesOptions, setNewThemesOptions] = useState<Map<string, string> | null>(null); // Maps theme ids to theme names

  /*   const storeStuff = () => write('englishToFrench', StoreType.Contents, 'myTheme', 'myContent');
    const readStuff = () => read('englishToFrench', StoreType.Contents, 'myTheme');
  
    useEffect(() => {
      const storeStuffAsync = async () => {
        if (!stuffStored) {
          await storeStuff();
          stuffStored = true;
          setReadyToRead(true);
        }
      };
      storeStuffAsync();
    }, []);
  
    useEffect(() => {
      if (readyToRead) {
        readStuff();
      }
    }, [readyToRead]);
   */
  return (
    <div className="App" style={{ alignItems: 'normal' }} >
      <Menu
        // onClick={onClick}
        style={{
          width: '15vw',
          position: "fixed",
          left: 0,
          top: '5vh'
        }}
        defaultSelectedKeys={['home']}
        defaultOpenKeys={['home']}
        mode="inline"
        items={items}
      />
      <div style={{ position: 'relative', left: '15vw', width: '85vw' }}>
        <div id='background'>
          <Outlet context={{ newThemesOptions, setNewThemesOptions }} />
          {/* <ChoicePage title='Choose an activity' firstOption='Learn something new' secondOption='Refresh my mind' /> */}
          {/* <ChoicePage title='Choose a new theme' firstOption='Theme1' secondOption='Theme2' /> */}
          {/* <LessonPage /> */}
          {/* <ExercisePage /> */}
          {/* <ScorePage /> */}
          {/* <ThemeCollectionPage themes={[...Array(10).keys()].map(v => 'theme' + v.toString())} /> */}
        </div>
      </div>
    </div>
  );
}

type ContextType = {
  newThemesOptions: Map<string, string> | null;
  setNewThemesOptions: React.Dispatch<React.SetStateAction<Map<string, string> | null>>;
};

export function useThemeOptions() {
  return useOutletContext<ContextType>();
}

export default App;
