import { useEffect, useState } from "react";
import Choice from "./Choice";
import { useThemeOptions } from "../App";
import Loading from "./Loading";

function buildThemesIds(themesNames: string[]): string[] {
    // Remove spaces and special characters, and use lower case
    return themesNames.map((themeName) => themeName.replace(/[^a-zA-Z0-9]/g, '').toLowerCase());
}

const ThemeChoicePage: React.FC<{}> = () => {
    const [finishedLoading, setFinishedLoading] = useState<boolean>(false);
    const [themesNames, setThemesNames] = useState<string[] | null>(null);
    const [themesIds, setThemesIds] = useState<string[] | null>(null);

    const { setNewThemesOptions } = useThemeOptions();

    useEffect(() => {
        // TODO: add GPT query
        const newThemesNames = ['Presentations', 'Family'];
        const newThemesIds = buildThemesIds(newThemesNames);
        setThemesIds(newThemesIds);
        setThemesNames(newThemesNames);
        setNewThemesOptions(new Map(newThemesIds.map((themeId, index) => [themeId, newThemesNames[index]])));
        // TODO: remove this timeout
        setTimeout(() => setFinishedLoading(true), 3000);
        // setFinishedLoading(true);
    }, [setNewThemesOptions]);

    if (!finishedLoading) {
        return (
            <Loading loadedTarget="themes" />
        );
    } else {
        if (themesIds === null) {
            throw Error("themesIds should not be null");
        }
        if (themesNames === null) {
            throw Error("themesNames should not be null");
        }
        return (
            <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
                <Choice
                    title='Choose a new theme'
                    firstLink={"/lesson/" + themesIds[0]}
                    secondLink={"/lesson/" + themesIds[1]}
                    firstLabel={themesNames[0]}
                    secondLabel={themesNames[1]}
                />
            </div>
        );
    }
}

                export default ThemeChoicePage;