import { Button, Input, Result, Space } from "antd";
import { useEffect, useState } from "react";
import { StoreName, read } from "../OfflineStorage";
import Loading from "./Loading";

function extractWords(content: string | null): string[] {
    return content ? Object.keys(JSON.parse(content)) : [];
}

function extractTranslations(content: string | null): string[] {
    return content ? Object.values(JSON.parse(content)) : [];
}

enum ExerciseStepType {
    NextWord = 'Next word',
    GoToScore = 'Go to score'
};

enum ResultStatus {
    Success = 'success',
    Error = 'error'
};

interface ExercisePageProps {
    themeId: string;
};

const ExercisePage: React.FC<ExercisePageProps> = (props) => {
    const [currentTranslation, setCurrentTranslation] = useState<string | null>(null);
    const [wordIndex, setWordIndex] = useState<number>(0);
    const [nextStepType, setNextStepType] = useState<ExerciseStepType>(ExerciseStepType.NextWord);
    const [translations, setTranslations] = useState<string[] | null>(null);
    const [words, setWords] = useState<string[] | null>(null);
    const [userInput, setUserInput] = useState<string | null>(null);
    const [resultStatus, setResultStatus] = useState<ResultStatus | null>(null);

    useEffect(() => {
        const readThemesIdsAsync = async () => {
            setWords(extractWords(await read('englishToFrench', StoreName.ThemesContents, props.themeId)));
        }
        const readThemesNamesAsync = async () => {
            setTranslations(extractTranslations(await read('englishToFrench', StoreName.ThemesContents, props.themeId)));
        }
        readThemesIdsAsync();
        readThemesNamesAsync();
    }, []);

    useEffect(() => {
        if (translations !== null) {
            setCurrentTranslation(translations![wordIndex]);
        }
    }, [wordIndex, translations])

    if (translations === null || words === null) {
        return (
            <Loading loadedTarget="exercise" />
        );
    } else {
        if (translations.length !== words.length) {
            throw Error(`themesIds and themesNames should have the same length. translations: ${translations} | words: ${words}`);
        }
        return (
            <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
                <div style={{ height: '20vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Let's test your knowledge!</div>
                <Space direction="vertical" size="middle">
                    <Space.Compact>
                        <div><i>{currentTranslation}</i></div>
                    </Space.Compact>
                    <Space.Compact>
                        <Input onChange={(event) => setUserInput(event.target.value)} />
                        <Button
                            type="primary"
                            onClick={() => {
                                if (userInput === words![wordIndex]) { // If input matches word, show success
                                    setResultStatus(ResultStatus.Success);
                                } else { // Else show error
                                    setResultStatus(ResultStatus.Error);
                                }
                            }}
                        >
                            Submit
                        </Button>
                    </Space.Compact>
                    {resultStatus === ResultStatus.Success && <Result
                        status="success"
                        title="Correct!"
                        extra={[
                            <Button
                                type="primary"
                                key="console"
                                onClick={() => {
                                    if (nextStepType === ExerciseStepType.NextWord) {
                                        if (wordIndex === words!.length - 1) {
                                            setNextStepType(ExerciseStepType.GoToScore);
                                        } else {
                                            setWordIndex(wordIndex + 1);
                                        }
                                    }
                                }}
                            >
                                Next
                            </Button>
                        ]}
                    />}
                    {resultStatus === ResultStatus.Error && <Result
                        status="error"
                        title="False!"
                        extra={[
                            <Button type="primary" key="console">
                                Next
                            </Button>
                        ]}
                    />}
                </Space>
            </div>
        );
    }
}

export default ExercisePage;
