import Choice from "./Choice";

const ActivityChoicePage: React.FC<{}> = () => {
    return (
        <div style={{ position: 'relative', left: '5vw', width: '70vw', height: '100vh', padding: '2vw', backgroundColor: 'white' }}>
            <Choice
                title='Choose an activity'
                firstLink="/new-theme"
                secondLink="/revisions"
                firstLabel='Learn something new'
                secondLabel='Refresh my mind'
            />
        </div>

    );
}

export default ActivityChoicePage;